<template>
    <div>
        <TopButtons/>
        <div style="height: 10px"></div>
        <a-spin :spinning="spinning">
            <a-card>
                <step :current="stepCurrent" :status="stepStatus"/>

                <info :id="thisId"/>

                <AuditForm v-if="stepCurrent===1" :id="thisId" v-model="form" @submit="submit"/>
            </a-card>
        </a-spin>
    </div>
</template>

<script>
    import TopButtons from '../会议列表/components/TopButtons'
    import Step from '@/components/meeting/step/Index'
    import Info from '@/components/meeting/info-list/Index'
    import AuditForm from '@/components/checkSuffixForm/audit'
    import {getMeeting, auditMeeting} from '@/api/meeting'

    export default {
        name: "Index",
        components: {
            TopButtons, Step, Info, AuditForm
        },
        props: {
            id: {
                required: true,
                type: [String, Number]
            },
        },
        data() {
            return {
                spinning: true,
                audit: null,
                form: {},
            }
        },
        computed: {
            thisId() {
                return parseInt(this.id);
            },
            stepCurrent() {
                if (this.audit === null) {
                    return -1;
                }

                if (this.audit.status === 0) {
                    return 1;
                } else if (this.audit.status === 1) {
                    return 2;
                } else {
                    return 2;
                }
            },
            stepStatus() {
                if (this.audit === null) {
                    return 'process';
                }

                if (this.audit.status === 0) {
                    return 'process';
                } else if (this.audit.status < 0) {
                    return 'error';
                } else {
                    return 'finish';
                }
            },
        },
        methods: {
            init() {
                if (!this.thisId) {
                    return;
                }

                getMeeting(this.thisId)
                    .then(res => {
                        this.audit = res.data.audit;
                        this.spinning = false;
                    })
            },
            submit(form) {
                let data = {
                    status: form.is_passed ? 1 : 2,
                    reason: form.reason
                };
                auditMeeting(this.id, data)
                    .then(() => {
                        let msg = '提交成功';
                        this.$message.success(msg);
                    })
                    .then(() => {
                        if (!form.is_passed) {
                            return false;
                        }

                        let _self = this;
                        this.$confirm({
                            title: '操作提示',
                            content: '审核成功！是否要跳转到下个审核步骤？',
                            onOk() {
                                _self.$router.push({name: '会议审核预约发送通知', params: {id: _self.thisId}})
                            },
                            onCancel() {
                            },
                        });
                    })
            }
        },
        mounted() {
            this.init();
        }
    }
</script>

