<template>
    <a-spin :spinning="spinning">
        <a-list
                :split="false"
                style="padding: 0 50px 20px 50px"
                itemLayout="horizontal"
                :dataSource="data"
        >
            <a-list-item slot="renderItem" slot-scope="item, index">
                <a-list-item-meta
                        :description="item.info"
                >
                    <a slot="title">{{item.title}}</a>
                </a-list-item-meta>
            </a-list-item>
        </a-list>
    </a-spin>
</template>

<script>
    import {getMeeting} from "@/api/meeting";

    export default {
        name: "Info",
        props: {
            id: {required: true, type: Number},
        },
        data() {
            return {
                spinning: true,
                data: []
            }
        },
        methods: {
            fetch() {
                this.spinning = true;
                if (this.id === 0) {
                    return;
                }

                getMeeting(this.id)
                    .then(res => {
                        this.data = [
                            {title: '会议时间', info: res.data.opened_at_format},
                            {title: '会议地点', info: res.data.location},
                            {title: '会议类型', info: res.data.type_format},
                            {title: '会议发起人', info: res.data.initiate_user.name},
                            {title: '审核人', info: res.data.audit.audit_user.name},
                            {title: '参会人员', info: res.data.meeting_attend_user_name_format},
                            {title: '参会领导', info: res.data.meeting_leader_name_format},
                        ];
                        this.spinning = false;
                    })
            },
        },
        mounted() {
            this.fetch();
        }
    }
</script>

<style scoped>
    .ant-list-item{
        background-color: #F7F4E9;
        margin-bottom: 5px;
        padding-left: 20px;

    }
    .list-item{
        background-color: #F7F4E9
    }
</style>
